import React from 'react'
import { useParams } from 'react-router-dom'
// import Blogs from './Blogs'

const User = ({ props }) => {
  const id = useParams().id

  const ShowOrhide = { display: props.visible ? '' : 'none' }

  const user = props.users.find(n => n.id === id)
  // const blogs = Blogs

  return(
    <div style={ShowOrhide}>
      <br />
      <b id='b-left'>{user.name}</b>
      <br />
      added blogs
      <div>
        <ul>
          {props.blogs.map(blog =>
            <li className='list-group-item' key={blog.id}>
              {blog.title}
            </li>
          )}
        </ul>
      </div>
      <br />
      <button type='button' id='back-button' className='btn btn-primary'
        onClick={() => {
          props.history.goBack()
          props.setVisible(!props.visible)
        }}
        props={{
          ...props,
        }} >back
      </button>
    </div>
  )
}

export default User
